}

/* Navbar Styles */

#side-nav,
#side-modal,
#side-modal-game {
  width: 100px;
}

#side-nav a,
#side-modal a,
#side-modal-game a{
  padding-left: 10px;
  padding-right: 10px;
}

#side-modal,
#side-modal-game {
  background-image: url(/static/media/dom-gradient.4a99fb7e.jpg);
  background-size: cover;
}

#side-nav {
  -webkit-transform: translateX(0%) !important;
          transform: translateX(0%) !important;
}

.logo-pic {
  position: relative;
  right: 25px;
  height: 100px;
}

#logo-side {
  width: 100%;
}

#logo-side-a {
  height: 60px;
  margin: 30px 20px 10px 20px !important;
  padding: 0px !important;
}

#logo-top {
  height: 60px;
  padding: 10px;
}

nav {
  height: 60px;
}

#top-nav {
  display: block;
}

#side-nav {
  display: none;
  -webkit-transform: translateX(0%) !important;
          transform: translateX(0%) !important;
}

.table-of-contents a.active {
  font-weight: 500;
  padding-left: 18px;
  border-right: 5px solid #8dc63f;
  color: #8dc63f !important;
  border-left: none;
}

.table-of-contents a:hover {
  font-weight: 500;
  padding-left: 18px;
  border-right: 5px solid #27aae1;
  color: #27aae1 !important;
  border-left: none;
  cursor: pointer;
}

/* Logo link styles */
.logo-link {
  height: 15vw;
  max-height: 60px;
  margin-left: 3vw;
  margin-right: 3vw;
  -webkit-animation: shrink .3s;
          animation: shrink .3s;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
}

.logo-link:hover {
  -webkit-animation: grow .3s;
          animation: grow .3s;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-transform: translate(0px, -5px) scale(1.3);
          transform: translate(0px, -5px) scale(1.3);

}

.pad-5 {
  padding: 10px;
}

/* Portfolio Styles */
#portfolio img {
  width: 100%;
}

#p-list li {
  line-height: 150%;
}

#move {
  padding-right: 10px;
}

#info-pics img {}

.carousel {
  height: 300px;
}

.carousel button {
  background-color: none;
  background: none;
  border: none;
  border-style: none;
  border-color: none;
  border-image: none;
}

.carousel .indicators .indicator-item {
  height: 8px;
  width: 8px;
  margin: 24px 4px;
  background-color: #212121;
  border-radius: 50%;
}

.carousel .indicators .indicator-item.active {
  background-color: #8dc63f;
}

.carousel-item {
  cursor: pointer;
}

.paint-pic:hover {
  -webkit-animation: rise .3s;
          animation: rise .3s;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-transform: translate(0px, -5px);
          transform: translate(0px, -5px);
}

/* About Styles */
.tool-pic {
  width: 50px;
}

.int-logo {
  width: 80px;
  margin-top: 10px;
  margin-bottom: 15px;

}

.bulge {
  -webkit-animation: bulge-in 1s;
          animation: bulge-in 1s;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-transform: scale(1.0);
          transform: scale(1.0);
}

.bulge:hover {
  -webkit-animation: bulge-out .001s;
          animation: bulge-out .001s;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.rotate {
  -webkit-animation: rotate .8s !important;
          animation: rotate .8s !important;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  /* transform: scale(1.1); */
}

/* Contact Styles */

.contact-link:hover {
  color: #27aae1 !important;
}

/* label focus color */
#contact-form .input-field input:focus+label {
  color: #27aae1 !important;
}

/* label underline focus color */
#contact-form .input-field input:focus {
  border-bottom: 1px solid #27aae1 !important;
  box-shadow: 0 1px 0 0 #27aae1 !important;
}

/* label focus color */
#contact-form .input-field textarea{
  height: 200px !important;
}

/* label focus color */
#contact-form .input-field textarea:focus+label {
  color: #27aae1 !important;
}

/* label underline focus color */
#contact-form .input-field textarea:focus {
  border-bottom: 1px solid #27aae1 !important;
  box-shadow: 0 1px 0 0 #27aae1 !important;
  /* height: 200px !important; */

  /* animation: extend 1s; */
  /* animation-iteration-count: 1; */
}

/* Media query changes */
.main-container {
  margin-left: 0px;

}

.parallax-container {
  height: 400px;
}

.logo-pic {
  position: relative;
  right: 25px;
  height: 100px;
  display: none;
}

#materialbox-overlay {
  opacity: .8 !important;
}

.page-footer {
  margin-bottom: -21px;
}

.footer-img {
  max-width: 30px;
  width: 10vw;
  margin: 0px 10px;
}

/* Game Styles */

#game-container {
  width: 100%;
  height: 100%;
  background-color: #212121;
  background-image: url(/static/media/map.c831363a.jpg);
  background-size: cover;
  position: fixed;
  z-index: 2;
  overflow: scroll;

  -webkit-animation: slide-in 2s;

          animation: slide-in 2s;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
}
#game-container::-webkit-scrollbar { width: 0 !important }

#game-form .form-input{
  color: white !important;
}

#game-form .input-field input:focus+label {
  color: #27aae1 !important;
}

#game-form .input-field input:focus {
  border-bottom: 1px solid #27aae1 !important;
  box-shadow: 0 1px 0 0 #27aae1 !important;
  color: white;
}

#game-form .input-field textarea{
  height: 200px !important;
}

#game-form .input-field textarea:focus+label {
  color: #27aae1 !important;
}

#game-form .input-field textarea:focus {
  border-bottom: 1px solid #27aae1 !important;
  box-shadow: 0 1px 0 0 #27aae1 !important;
}

.game-choice-btn {
  color: white;
  display: block;
}
.game-item-btn {
  color: white;
  display: inline;
}

.game-choice-btn:hover{
  color: #27aae1;
  background-color: transparent;
}
.game-item-btn:hover{
  color: #27aae1;
  background-color: transparent;
}

.game-blue-btn {
  color: #27aae1;
  margin-right: 10px;
  background-color: transparent;
}
.game-blue-btn:hover{
  color: white;
  background-color: #27aae1;
}
.disabled-div {
    pointer-events: none !important;
    color: grey !important;
}
.disabled-div button{
    pointer-events: none !important;
    color: grey !important;
}
.fontSmall {
  font-size: 80%;
  margin-left: -15px;
  z-index:-10;
}

.game-info {
  margin: 5px 0px 5px 0px;
  padding: 8px 15px 8px 15px;
  border: .5px solid grey;
  min-height: 102px;
}

.fade {
  -webkit-animation: fade-in 5s;
          animation: fade-in 5s;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
}

@media only screen and (min-width: 600px) {

  #top-nav,
  #top-nav-container {
    display: none;
  }

  #side-nav {
    display: block;
  }

  .main-container {
    margin-left: 99px;
  }

  .parallax-container {
    height: 500px;
  }

  .logo-pic {
    display: inline;
  }

  .logo-link {
    height: 60px;
    margin-left: 25px;
    margin-right: 25px;
  }

  #move {
    padding-right: 18px;
  }

  #game-container {
    width: calc(100% - 99px);
  }
}

@-webkit-keyframes grow {
  0% {
    -webkit-transform: translate(0px, 0px) scale(1.0);
            transform: translate(0px, 0px) scale(1.0)
  }

  100% {
    -webkit-transform: translate(0px, -5px) scale(1.3);
            transform: translate(0px, -5px) scale(1.3)
  }
}

@keyframes grow {
  0% {
    -webkit-transform: translate(0px, 0px) scale(1.0);
            transform: translate(0px, 0px) scale(1.0)
  }

  100% {
    -webkit-transform: translate(0px, -5px) scale(1.3);
            transform: translate(0px, -5px) scale(1.3)
  }
}

@-webkit-keyframes shrink {
  0% {
    -webkit-transform: translate(0px, -5px) scale(1.3);
            transform: translate(0px, -5px) scale(1.3)
  }

  100% {
    -webkit-transform: translate(0px, 0px) scale(1.0);
            transform: translate(0px, 0px) scale(1.0)
  }
}

@keyframes shrink {
  0% {
    -webkit-transform: translate(0px, -5px) scale(1.3);
            transform: translate(0px, -5px) scale(1.3)
  }

  100% {
    -webkit-transform: translate(0px, 0px) scale(1.0);
            transform: translate(0px, 0px) scale(1.0)
  }
}

@-webkit-keyframes rise {
  0% {
    -webkit-transform: translate(0px, 0px);
            transform: translate(0px, 0px)
  }

  100% {
    -webkit-transform: translate(0px, -5px);
            transform: translate(0px, -5px)
  }
}

@keyframes rise {
  0% {
    -webkit-transform: translate(0px, 0px);
            transform: translate(0px, 0px)
  }

  100% {
    -webkit-transform: translate(0px, -5px);
            transform: translate(0px, -5px)
  }
}

@-webkit-keyframes extend {
  0% {
    height: 10px;
  }

  100% {
    height: 200px;
  }
}

@keyframes extend {
  0% {
    height: 10px;
  }

  100% {
    height: 200px;
  }
}

@-webkit-keyframes bulge-out {
  0% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1)
  }

  100% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1)
  }
}

@keyframes bulge-out {
  0% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1)
  }

  100% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1)
  }
}

@-webkit-keyframes bulge-in {
  0% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1)
  }

  100% {
    -webkit-transform: scale(1.0);
            transform: scale(1.0)
  }
}

@keyframes bulge-in {
  0% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1)
  }

  100% {
    -webkit-transform: scale(1.0);
            transform: scale(1.0)
  }
}

@-webkit-keyframes fade-in {
  0% {
    opacity: 0;
  }

  40% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes fade-in {
  0% {
    opacity: 0;
  }

  40% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}


@-webkit-keyframes rotate {
  from {
    -webkit-transform: rotate(0deg) scale(1.1);
            transform: rotate(0deg) scale(1.1)
  }

  to {
    -webkit-transform: rotate(359deg) scale(1.1);
            transform: rotate(359deg) scale(1.1)
  }
}


@keyframes rotate {
  from {
    -webkit-transform: rotate(0deg) scale(1.1);
            transform: rotate(0deg) scale(1.1)
  }

  to {
    -webkit-transform: rotate(359deg) scale(1.1);
            transform: rotate(359deg) scale(1.1)
  }
}

@-webkit-keyframes slide-in {
  0% {
    width: 0%;
  }

  100% {
    width: 100%
  }
}

@keyframes slide-in {
  0% {
    width: 0%;
  }

  100% {
    width: 100%
  }
}
